<template>
  <div class="register">
    <van-nav-bar
      title="登录"
      left-text="注册"
      right-text="关闭"
      @click-left="registers('Register')"
      @click-right="goMenu"
    />
    
    <div class="logo">
      <img src="../assets/images/a.png" class="auto-img"/>
    </div>

    <van-form class="form" @submit="register1">
        <van-field
        type="text"
        v-model="userinfo.phone"
            label="手机号"
            placeholder="请输入11位手机号"
            left-icon="phone-o"
            maxlength='11'
        >
        </van-field>

             <van-field
                :type="addNum?'text':'password'"
                label="密码"
                v-model="userinfo.password"
                placeholder="字母数字_组合(6-16位)"
                left-icon="lock"
                :right-icon="addNum?'eye-o':'closed-eye'"
                @click-right-icon="passWd"
                maxlength="16"
            >

             </van-field>

        <div class="sub_btn">
            <van-button round block type="info" native-type="submit">
            登录
            </van-button>

            <div class="paws">
                <span class="fr" @click="goLosePW">忘记密码?</span>
            </div>
        </div>
   </van-form>

      <!-- <van-dialog title="阿里路亚">
      </van-dialog> -->
  </div>
</template>

<script>

  //导入验证表单模块
  import validForm from '../assets/js/validForm'

  export default {
      data(){
        return {
          userinfo: {
            phone: '',
            password: ''
          },
          addNum: false
        }
      },
      computed:{
       
      },
      methods:{
        registers(name){
            this.$router.push({name})
        },
        passWd(){
          this.addNum=!this.addNum;
        },
        
        register1(){
          //进行验证表单
          
          if (validForm.validUserForm(this.userinfo, this)) {
            

           
            let data = Object.assign({}, this.userinfo);

          
            data.appkey = 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=';

          
            this.$toast.loading({
              message: '加载中...',
              forbidClick: true,

              duration:0
            });

            //发起登录验证
            this.axios({
              method: 'POST',
              url: 'http://www.kangliuyong.com:10002/login',

           
              data
            }).then(result => {
             
              this.$toast.clear();
              
              if(result.data.code==200){
                
                  

                  
                  localStorage.setItem('__Tk',result.data.token);
                  this.$router.push({name: 'HomePage'});

              }else{
                  this.$toast(result.data.msg);
              }
            }).catch(err => {
              
              this.$toast.clear();
              
            })
          }
        },
        goMenu(){
          this.$router.push({name: 'HomePage'});
        },
        goLosePW(){
          this.$router.push({name: 'LosePW'});
        }
      }
  }
</script>

<style lang="less" scoped>
   .logo{
    width: 120px;
    height: 120px;
    margin: 50px auto;
  }
  .sub_btn{
    margin: 50px;
  }

  .paws{
      margin: 10px 5px 0px 0px;
  }
</style>